#root {
  margin-top: 50px;
  justify-content: center;
  display: flex;
}

.root__line {
  margin-top: 20px;
  border-bottom: 5px solid black;
}

.root__container-header{
  background-image: url("https://w-hatsapp.ru/wp-content/uploads/2015/10/fon-8.png");
}

.root__main-container {
  height: 500px;
  width: 300px;
  border: solid 5px black;
  border-radius: 25px;
  justify-content: center;
}

.root__avtoriz-container {
  height: 550px;
  width: 300px;
  border: solid 5px black;
  border-radius: 25px;
  justify-content: center;
}

.avtoriz-container__header-div {
  justify-content: center;
  display: flex;
  font-size: 20px;
  font-family: sans-serif;
}

.header-div__header-text {
  margin-top: 70px;
  font-family: "Cabin", Helvetica, Arial, sans-serif;
  color: grey;
}

.window__login-body {
  margin-top: 50px;
  display: flex;
  justify-content: center;
}

.login-input-div__login-input {
  height: 40px;
  width: 200px;
  padding: 10px;
  font-size: 20px;
  font-family: Georgia, "Times New Roman", Times, serif;
  border: 1px solid #fff;
  background: rgba(255, 255, 255, 0.25);
  border-radius: 20px;
  outline: none;
}

.avtoriz-container__login-button-div {
  padding-bottom: 145px;
  border-bottom: 5px solid black;
  justify-content: center;
  display: flex;
}

.login-button-div__login-button {
  height: 50px;
  width: 90px;
  margin-top: 10px;
  background-color: #8b799e;
  border: 1px solid #7a6390;
  border-radius: 20px;
  outline: none;
}

.avtoriz-container__container-btn {
  display: flex;
  justify-content: center;
}

.container-btn__btn-circle {
  height: 50px;
  width: 50px;
  margin-top: 4px;
  border-radius: 50%;
  border: 2px solid black;
  outline: none;
}